.price {
  width: 30rem;
  background-color: $primaryColor;
  border-radius: 1rem;
  padding: 1.6rem 0.2rem 2rem;
  &__title {
    @include h3($white);
    text-align: center;
    margin-bottom: 1.7rem;
    line-height: 1;
  }
  &__sum {
    width: 100%;
    height: 7.1rem;
    font-size: 4rem;
    text-align: center;
    line-height: 1;
    color: $primaryColor;
    background-color: $white;
    padding: 1.8rem 0;
    margin-bottom: 1.5rem;
    &::before {
      content: "$";
    }
  }
  &__features {
    @include p2($white);
    line-height: 1.71;
    padding-left: 3.3rem;
    padding-right: 2.8rem;
    margin-bottom: 1.5rem;
  }
  &__btn {
    @extend %btn;
    display: block;
    margin: 0 1.8rem;
  }
}
