.review {
  display: flex;
  flex-direction: column;
  align-items: center;
  @media ($tablet) {
    flex-direction: row;
  }
  &__img {
    @include img();
    width: 7.5rem;
    height: 7.5rem;
    border-radius: 50%;
    margin-bottom: 2.6rem;
    @media ($tablet) {
      flex-shrink: 0;
      margin-bottom: 0;
      margin-right: 2.7rem;
    }
  }
  &__txt {
    @include p2();
    position: relative;
    background-color: $grey2;
    border-radius: 1rem;
    padding: 1.6rem 2rem 1.2rem 1.5rem;
    &::before {
      content: "";
      position: absolute;
      top: -1rem;
      left: calc(50% - 1rem);
      background-color: $grey2;
      width: 2rem;
      height: 2rem;
      transform: rotate(45deg);
      @media ($tablet) {
        left: -1rem;
        top: 2.8rem;
      }
    }
    &-item {
      font-style: italic;
      margin-bottom: 0.8rem;
    }
    &-author {
      color: $grey1;
    }
  }
}
