// Titles
.h1 {
  @include h1();
}

.h2 {
  @include h2($align: left);
}

.h3 {
  @include h3();
}

// Text
.lead {
  @include lead();
}

.p1 {
  @include p1();
}

.p2 {
  @include p2();
}

// Buttons
.btn {
  @extend %btn;
}

// Forms
.input {
  @extend %input;
}

.textarea {
  @extend %textarea;
}
