// Titles
@mixin h1($color: $white) {
  color: $color;
  font-size: 4rem;
  font-family: $mainFont;
  text-shadow: $mainShadow;
  margin-bottom: 3rem;

  @media ($tablet) {
    font-size: 6rem;
    margin-bottom: 5rem;
  }
}

@mixin h2($color: $primaryColor, $align: center) {
  color: $color;
  text-align: $align;
  font-family: $mainFont;
  font-size: 3rem;
  line-height: 1.2;
  margin-bottom: 3rem;
}

@mixin h3($color: $primaryColor) {
  color: $color;
  font-size: 2.4rem;
  line-height: 1.25;
}

// Text
@mixin lead($color: $white) {
  color: $color;
  text-shadow: $mainShadow;
  font-size: 2rem;
  line-height: 1.8;
}

@mixin p1($color: $textColor) {
  color: $color;
  font-size: 1.6rem;
  line-height: 1.5;
}

@mixin p2($color: $textColor) {
  color: $color;
  font-size: 1.4rem;
  line-height: 1.29;
}

// Picture
@mixin img($bg: $primaryColor) {
  background-color: $bg;
  object-fit: cover;
  object-position: center;
}

// Buttons
%btn {
  display: inline-block;
  color: $primaryColor;
  font-size: 1.8rem;
  font-weight: bold;
  line-height: 1;
  text-transform: uppercase;
  text-align: center;
  background-color: $white;
  border-radius: 0.5rem;
  padding: 1.4rem 4.8rem;
  cursor: pointer;
  border: none;
  text-decoration: none;
  user-select: none;
  &:hover {
    background-color: darken($white, 20%);
  }
  &:focus {
    outline: none;
  }
}

// Forms
%input {
  color: $secondaryColor;
  font-size: 1.6rem;
  background-color: $white;
  padding: 1.4rem 1rem;
  line-height: 1;
  border: none;
  border-radius: 0.5rem;
}

%textarea {
  @extend %input;
  height: 17rem;
  resize: none;
}
